@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body {
  background: linear-gradient(0deg, #e0c3fc 0%, #8ec5fc 100%) no-repeat;
  color: #353e5d;
  min-height: 100vh;
  display: grid;
  place-items: center;
  padding: 1rem;
}

